<template>
    <div class="banner-wrapper" id="banner" :style="`background-image: url(${bgUri})`">
      <div class="contents" v-html="contents">
      </div>
    </div>
</template>

<script>
  export default {
    name: "Banner",
    props: [
      "bgUri",
      "contents"
    ]
  }
</script>

<style src="../style/components/Banner.css" scoped></style>



