<template>
  <div class="card-wrapper" id="card">
      <router-link :to="{path: '/estimate', query: { item: contents.idx  } }">
       <span class="bage">{{ contents.bage }}</span>
        <img :src="contents.cover">
        <div class="body">
          <div class="title">
              {{ contents.item_name }}
          </div>
          <div class="contents" v-text="contents.text"></div>
        </div>
      </router-link>
  </div>
</template>

<script>

export default {
  name: 'Card',
  props: [
    'contents'
  ]
}
</script>

<style src="../style/components/Card.css" scoped></style>



