import VueRouter from 'vue-router'
import Home from '../spa/Home'
import Categori from '../spa/Categori'
import Estimate from '../spa/Estimate'
import Policy from '../spa/Policy'
import PrivacyPolicy from '../spa/PrivacyPolicy'

const routes = [
  {
      path: '/',
      component: Home,
      meta: {
          title: 'comfit',
      }
  },
  {
    path: '/categori',
    component: Categori,
    meta: {
      title: '카테고리 - comfit',
    }
  },
  {
    path: '/estimate',
    component: Estimate,
    meta: {
      title: '견적내기 - comfit',
    }
  },
  {
    path: '/policy',
    component: Policy,
    meta: {
      title: '서비스 이용약관 - comfit',
    }
  },
  {
    path: '/privacyPolicy',
    component: PrivacyPolicy,
    meta: {
      title: '서비스 이용약관 - comfit',
    }
  }
]

const router = new VueRouter({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  mode: 'history',
  routes
})

router.beforeEach(function (to, from, next) {
  if(to.meta && to.meta.title){
    document.title = to.meta.title;
  }
  return next();
});

export default router



