<template>
  <div id="home">
    <app-nav></app-nav>
    <app-categori-nav></app-categori-nav>
    <app-banner
      :bgUri="'/images/banner/'+bgUri"></app-banner>
    <div class="categori-group">
      <router-link
        v-for="(item, idx) in items[categori]"
        :to="{path: '/estimate', query: { item: item.id  } }"
        :key="idx">
        <app-categori :item="item"></app-categori>
      </router-link>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
  import Nav from '../components/Nav'
  import Footer from '../components/Footer'
  import CategoriNav from '../components/CategoriNav'
  import Banner from '../components/Banner'
  import Categori from '../components/Categori'

  export default {
    name: 'home',
    components: {
      "app-nav": Nav,
      "app-footer": Footer,
      "app-categori-nav": CategoriNav,
      "app-banner": Banner,
      "app-categori": Categori
    },
    computed: {
      categori() {
        return this.$route.query.categori;
      },
      bgUri() {
        switch(this.categori) {
          case 'game': return 'banner_game.png';
          case 'doc': return 'banner_doc.png';
          case 'movie': return 'banner_movie.png';
          case 'program': return 'banner_program.png';
          case 'etc': return 'banner_etc.png';
          default: return 'banner.png';
        }
      }
    },
    data() {
      return {
        items: {
          game: [
            {
              id: "pubg",
              title: "배틀그라운드",
              contents: "contents",
              img: "pubg.png"
            },
            {
              id: "lol",
              title: "리그오브레전드",
              contents: "contents",
              img: "lol.png"
            },
            {
              id: "overwatch",
              title: "오버워치",
              contents: "contents",
              img: "overwatch.png"
            }
          ],
          doc: [
            {
              id: "ms_office",
              title: "마이크로 오피스",
              contents: "contents",
              img: "ms_office.png"
            },
            {
              id: "hwp",
              title: "한글과 컴퓨터",
              contents: "contents",
              img: "hwp.png"
            }
          ],
          movie: [
            {
              id: "pr",
              title: "프리미어프로",
              contents: "contents",
              img: "pr.png"
            },
            {
              id: "after",
              title: "에프터이펙트",
              contents: "contents",
              img: "movie_unknown.png"
            }
          ],
          program: [
            {
              id: "java",
              title: "Java",
              contents: "contents",
              img: "java.png"
            },
            {
              id: "android_studio",
              title: "안드로이드 스튜디오",
              contents: "contents",
              img: "android_studio.png"
            }
          ]
        }
      }
    }
  }
</script>

<style src="../style/spa/Categori.css" scoped></style>



