<template>
  <div class="categori" id="categori">
    <div class="categori__header">
      <div class="categori__header_title">{{ item.title }}</div>
      <div class="categori__header_contents">{{ item.contents }}</div>
    </div>
    <img class="categori__image" :src="`/images/categori/${item.img}`">
  </div>
</template>

<script>

export default {
  name: 'Categori',
  props: [
    "item"
  ]
}
</script>

<style src="../style/components/Categori.css" scoped></style>



