var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_c('h3',[_vm._v("서비스 이용약관")]),_vm._v(" "),_c('pre',[_vm._v("제 1장 총칙\n제1조 (목 적)\n 컴핏 사이트( www.comfit.com, 이하“아이티스트”라 한다) 회원가입 이용약관 (이하“약관”이라 한다)은 고객(이하“회원”이라 한다)이 아이티스트(이하“회사”라 한다)가 제공하는 정보제공서비스(이하“서비스”라 한다)를 이용함에 있어 회원과 회사간의 권리, 의무 및 책임사항을 규정함을 목적으로 합니다.\n\n제 2 조 (용어의 정의)\n본 약관에서 규정하는 용어의 정의는 다음 각호와 같습니다. 단 다음 각호 외의 용어는 관계법령 및 서비스 별 안내에서 정하는 바에 의합니다.\n가.‘서비스’란 회사와 계약을 통하여 입점 및 제휴한 쇼핑몰들의 상품가격정보를 비교하여 회원의 구매를 돕는 서비스 및 시뮬레이션 경매시스템 기타 각종 서비스를 의미합니다.\n나.‘이용자’란 컴핏에 접속하여 회사가 제공하는 서비스를 이용하는 자로서 정식 회원이 되기 이전의 자를 의미합니다.\n다.‘회원’이란 이용자 중에서 본 약관에 동의를 한 후 회사가 필요로 하는 최소한의 개인정보를 허위 없이 기재하여 회사의 승낙을 얻은 자를 의미합니다.\n라.‘이용자번호 (ID)’라 함은 회원의 식별과 서비스 이용을 위하여 회원이 선정하고 회사가 부여하는 문자와 숫자의 조합을 말합니다.\n마.‘비밀번호’라 함은 회원이 부여 받은 이용자번호(ID)와 일치된 회원임을 확인하고 회원의 권익보호를 위하여 선정한 문자와 숫자의 조합을 말합니다.\n바.‘게시물’이라 함은 회원이 회사의 서비스를 이용하면서 작성한 모든 활동내역을 의미합니다.\n\n제 3 조 (약관의 효력 및 변경)\n①이용자가 회원이 되기 위해서는 회사가 제시한 각종 절차에 대해 동의를 한 후 회사의 승낙을 얻어야 합니다.\n②이용자가 ‘동의함’ 버튼을 누르면 본 약관을 모두 이해하고 그 내용에 동의하는 것으로 간주합니다.\n③회사가 제공하는 서비스 전반에 있어 책임의 한계와 개인정보취급방침 및 개별 서비스 이용약관에 관하여는 별도 고지합니다.\n④회사는 전자거래기본법, 전자서명법, 정보통신망 이용촉진 등에 관한 법률 등 관계법령을 위배하지 않는 범위에서 합리적 사유가 있는 경우 회원의 사전 동의 없이 본 약관을 개정할 수 있습니다.\n⑤본 약관의 개정은 효력 발생일 15일 전에 공지하며, 공지 후 효력발생일 이전까지 거부 의사 표시 또는 탈퇴를 하지 않을 경우 개정 약관에 동의하는 것으로 간주합니다. 단 회사는 개정된 약관 사항에 동의하지 않은 회원에 대하여 서비스 이용을 중단시킬 수 있습니다.\n⑥회원은 수시로 컴핏을 방문하여 약관의 변경사항을 확인해야 하며, 이를 게을리 하여 개정된 약관에 대한 정보를 알지 못해 발생하는 회원의 피해는 회사가 책임지지 않습니다.\n\n\n\n제2장 회원가입\n제 4 조 (회원 가입)\n①아이티스트의 회원가입은 별도의 고지가 있을 때 까지 무료이며, 회원이 되고자 하는 이용자는 회사가 채택한 실명인증 절차를 거친 후 회사가 정한 가입 양식에 따라 진정(眞正)한 회원정보를 기입하고, ‘동의함’ 버튼을 누르는 방식으로 가입합니다.\n②아이티스트의 회원은 대한민국 국민 중 만14세 이상에 한하여 가입이 승인됩니다.\n③회사는 제1항 및 제2항의 절차를 거쳐 회원 가입을 신청한 이용자 중 다음 각 호에 해당하지 않는 한 회원으로 가입을 승인 합니다.\n    가. 회원가입 신청자가 과거 회원 자격을 상실한 적이 있는 경우(회사로부터 회원자격의          제재조치를 받고 임의탈퇴 또는 강제탈퇴 한 경우를 포함).\n    나. 회원 가입시 기재한 내용에 허위, 누락, 오기가 있는 경우 또는 비(非)실명으로 가입          을 하거나 이를 시도한 경우.\n    다. 기타 회원으로 가입하는 것이 회사 서비스에 현저한 지장이 있다고 판단되는 경우.\n    라. 최초 회원가입 시 등록한 전자우편 주소와 관련하여, 회사가 해당 전자우편을 통하여         각종 자료 등을 발송하는 것이 불가능 하거나 별도의 발송수수료가 수반될 경우.\n④회원가입을 통한 이용계약 체결의 성립 시기는 회사의 승낙이 가입신청자에게 도달한 시점으로 합니다.\n\n제 5 조 (개인정보 보호)\n①회사는 관계법령이 정하는 바에 따라 회원 등록정보를 포함한 회원의 개인정보를 보호하기 위해 노력합니다.\n②회사는 회원의 동의를 받은 후 회원 정보를 수집하며, 수집된 정보는 회원의 동의를 얻은 범위 내에서만 이용합니다.\n③회원 개인정보 보호 및 이용에 대해서는 회사의 개인정보취급방침을 따릅니다. 단 개인정보취급방침에 규정되어 있지 않은 것\n    은 관계법령에 따릅니다.\n\n제 6 조 (이용자번호 ID)\n①회사가 관계법령 및 개인정보취급방침에 의해서 그 책임을 지는 경우를 제외하고, 자신의 ID와 비밀번호에 관한 관리책임은 회원에게 있습니다. 회원은 자신의 ID 및 비밀번호를 본인 외의 제3자에게 공개하거나 이용하게 해서는 안됩니다.\n②회원은 자신의 ID 및 비밀번호를 도난 당하거나 제3자가 이용하고 있음을 인지한 경우 즉시 회사에 통보하고, 회사의 안내에 따릅니다.\n③회원은 제1항 및 제2항의 규정을 준수해야 하며, 이를 위반하여 회원에게 발생하는 모든 불이익에 대해 회사는 도의적·법적 책임을 부담하지 않습니다.\n\n제 7 조 (회원정보의 변경)\n①회원은 회원가입 시 기재한 회원정보가 변경되었을 경우 온라인으로 직접 수정을 하거나, 다나와 관리자에게 수정을 요청할 수 있습니다. 단 회원정보를 변경하지 않거나 허위 또는 오기로 인해 발생되는 문제는 회원에게 책임이 있습니다.\n②회원이 회사에 제공한 회원정보를 알지 못하여 회사의 서비스를 이용하지 못하게 된 회원은 회원정보 수정페이지를 통하여 정해진 양식을 작성하여 회사에 제출하는 방식으로 이를 해결할 수 있습니다.\n\n제 8 조 (회원 탈퇴 및 자격정지, 상실)\n①회원은 언제든지 회원가입에 대한 탈퇴를 할 수 있으며, 그 방법은 다음 각호의 같습니다.\n    가. 컴핏에 로그인 한 후에 정보수정 페이지에서 절차에 따라 회원 탈퇴를 할 수 있습니          다.\n    나. 회원의 개인정보 보유와 파기여부에 관하여는 관련법령 및 회사가 별도로 정한 개인          정보취급방침에 의합니다.\n②회원이 다음 각호의 사유 또는 아이티스트 이용규칙에 해당할 경우, 회사는 사전통지 없이 회원가입을 취소, 정지 및 강제 탈퇴를 시키거나 법적 조치를 취할 수 있습니다. 단 다음 각호의 사유는 아이티스트 이용규칙으로 인해 변경될 수 있으며, 이 경우 회원의 제재는 회원의 별도 동의 없이 변경된 아이티스트 이용규칙을 따릅니다.\n        가. 회원가입 신청 또는 회원정보 변경 시 허위내용을 등록하는 행위.\n        나. 타 회원의 ID, 비밀번호, 주민등록번호 등 개인정보를 도용 또는 거래하는 행위.\n        다. 회사의 운영진, 직원 또는 관계자를 사칭하는 행위.\n        라. 타인의 명예를 훼손하거나 모욕하는 행위를 하는 경우.\n        마. 회사의 서비스 운영에 고의 또는 과실로 불이익이나 차질을 주는 경우.\n        바. 서비스를 통해 얻은 정보를 회사의 사전승낙 없이 이용 또는 제3자에게 제공하는              행위.\n        사. 공공질서 및 미풍양속에 위반되는 게시물을 게재하거나 유포하는 경우.\n        아. 회사, 회원 및 제3자의 인격권 또는 저작권 등 관계법령에 위반되는 불법적인 행              위를 하는 경우.\n        자. 다른 회원 및 이용자를 희롱 또는 위협하거나, 특정 회원 및 이용자에게 지속적              으로 고통 또는 불편을 주는 행위\n        차. 회사가 주최하는 이벤트에 요구되는 의무를 이행하지 않는 경우.\n        카. 본 약관을 포함하여 기타 회사가 정한 제반 규정 또는 관계법령을 위반하는 행               위.\n③회원은 전 항의 귀책사유로 인하여 회사나 다른 회원 및 이용자에게 입힌 손해를 배상할 민사상의 책임이 있으며, 범죄 행위에 대해서는 형사상의 책임을 부담합니다.\n\n제 3장 계약 당사자의 권리 의무\n 제 9 조 (회사의 의무)\n①회사는 관계법령과 본 약관이 정하는 바에 따라 안정적인 서비스를 제공할 수 있도록 하기 위하여 최선을 다합니다.\n②회사는 전자보증제도, 에스크로 등과 같은 시스템 도입 등을 통하여 회원의 안전한 거래를 적극 도모하며, 이와 관련한 관계법령 을 준수합니다.\n③회사는 회원이 원하지 않는 영리목적의 광고성 전자우편을 발송하지 않습니다.\n④회사는 회원이 게재한 게시물이 적법하고 내용상 정확한 것인지 확인할 의무가 없습니다.\n\n\n제 10 조 (회원의 의무)\n①회원은 회원가입 신청 또는 회원정보 변경 시 실명으로 모든 사항을 사실에 근거하여 작성하며, 허위 또는 타인의 정보를 등록할 경우 일체의 권리를 주장할 수 없습니다.\n②회원은 본 약관에서 규정하는 사항과 회사가 정한 제반 규정, 공지사항, 그 밖의 관계법령 등을 준수하여야 하며, 회사의 명예를 손상시키는 행위, 기타 회사 업무에 방해가 되는 행위를 해서는 안됩니다.\n③회원은 회사의 명시적 동의가 없는 한 서비스의 이용권한, 기타 이용 계약상의 지위를 타인에게 양도, 담보, 증여를 할 수 없습니다.\n④회원은 다나와의 서비스를 통한 주문, 대금결제, 배송 및 취소 등 상품 구매와 관련된 일체의 절차는 회원이 주문한 쇼핑몰의 절차에 따라야 하며, 그 과정상 발생되는 분쟁 및 기타 문제는 회원과 해당 쇼핑몰이 직접 해결해야 합니다.\n⑤회원은 본 약관 제8조 제2항 각호의 행위를 하여서는 안됩니다.\n\n제 4장 서비스 이용\n제 11 조 (서비스 제공 범위 및 한계)\n①아이티스트의 컴핏 사이트는 국내 상품의 정보와 가격 정보를 비교하여 제공함으로써 소비자가 스스로 시뮬레이션을 통해 역경매 방식으로 효율적으로 쇼핑을 돕는 참조 사이트입니다.\n②회사는 다양한 상품을 검색해 주는 기능과 더불어 회원의 자유로운 온라인 시장인 마켓서비스를 제공합니다.\n③회사는 회원간의 활발한 정보공유의 장(場) 마련을 위하여 커뮤니티와 블로그 서비스를 제공합니다.\n④회사는 컴핏에 입점해 있는 쇼핑몰 (판매자)과 회원간에 발생할 수 있는 전자상거래 상의 분쟁에 대하여 일체 개입하지 않으며,이에 대한 어떠한 법적·도의적 책임도 부담하지 않습니다.\n⑤회원은 회사가 별도 고지한 서비스에 있어서 블로그 이용약관, 마켓 이용규칙, 개인정보취급방침 및 기타 회사가 공지하는 사항을 항상 숙지해야 하며, 이를 숙지하지 못해 생기는 불이익을 회원에게 있습니다.\n\n제 12 조 (서비스 제공 및 중단)\n①서비스 이용은 회사의 업무상 또는 기술상 특별한 지장이 없는 한 연중 무휴, 1일 24시간 운영을 원칙으로 합니다. 단, 회사는 시스템 정기점검, 증설 및 교체를 위해 회사가 정한 날이나 시간에 서비스를 일시 중단할 수 있으며, 예정되어 있는 작업으로 인한 서비스 일시 중단은 사전에 공지합니다.\n②회사는 긴급한 시스템 점검, 증설 및 교체 등 부득이한 사유로 인하여 예고 없이 일시적으로 서비스를 중단할 수 있으며, 새로운 서비스의 교체 등 회사가 적절하다고 판단하는 사유에 의하여 현재 제공되는 서비스를 완전히 중단할 수 있습니다.\n③회사는 국가 비상사태, 천재지변, 정전, 서비스 설비의 장애 또는 서비스 이용의 폭주 등으로 정상적인 서비스가 불가능할 경우,  서비스의 전부 또는 일부를 제한하거나 중지할 수 있습니다. 다만 이 경우 그 사유 및 기간 등을 회원에게 사후 공지합니다.\n④회사는 통제할 수 없는 사유로 인한 서비스 중단의 경우 (시스템 관리자의 고의, 과실 없는 디스크장애, 시스템 다운 등)에 사전통지가 불가능하며, 타인(PC통신회사, 기간통신사업자 등)의 고의, 과실로 인한 시스템 중단 등의 경우에는 통지하지 않습니다.\n⑤회사는 서비스를 특정범위로 분할하여 각 범위 별로 이용가능 시간을 별도로 지정할 수 있습니다. 다만 이 경우 그 내용을 공지합니다.\n\n제 13 조 (회원의 게시물)\n①서비스 내 게시된 게시물로 인해 회원에게 발생하는 손실이나 분쟁은 전적으로 회원의 책임이며, 회사는 이에 대해 어떠한 책임도 부담하지 않습니다.\n②회원이 타인의 저작권, 프로그램 저작권 등을 침해하였음을 이유로 회사가 타인으로부터 손해배상청구 등의 이의제기를 받은 경우 회원은 회사를 면책시키고 모든 손해를 배상해야 하며, 회사에 발생한 손해에 대해서도 그 책임을 부담합니다.\n③회원의 게시물 내용이 다음 각호의 사유 또는 다나와 이용규칙에 해당하는 경우 회사는 해당 회원에게 사전통지 없이 게시물을\n    수정 및 삭제 조치를 할 수 있으며, 회원은 본 약관 제8조 제2항에 따라 제재를 받게 됩니다. 단 다음 각호의 사유는 컴핏 이용규칙으로 인해 변경될 수 있으며, 이 경우 회원의 제재는 회원의 동의 없이 변경된 다나와 이용규칙을 따릅니다.\n\n    가. 타인을 비방하거나 명예를 훼손, 모욕하는 내용.\n    나. 미풍양속에 위반되는 내용.\n    다. 불법적이거나 범죄행위와 관련이 있다고 판단되는 내용.\n    라. 타 회원 또는 제3자의 저작권 등 기타 권리를 침해하는 내용.\n    마. 영리를 목적으로 하는 광고 등 상업적 이용이라고 판단되는 내용.\n    바. 회사에서 규정한 게시물 원칙에 어긋나거나 게시판 성격에 부합하지 않는 내용.\n    사. 종교적, 정치적 분쟁을 야기할 가능성이 있는 내용.\n    아. 기타 관계 법령에 위배된다고 판단되는 내용.\n\n④제3항에 해당하는 게시물의 작성자와 문제를 해결하고자 할 경우, 회원은 회사 운영자에게 전자우편 등의 방법으로 본인의 성명, 아이디, 주소, 연락처, 신고사유 등을 작성하여 신고할 수 있습니다.\n⑤문제 회원의 제재 조치는 사안의 경중에 따라 변경이 가능하며, 이에 대한 이의제기는 동(同) 조치 후 7일 이내에 전 항의 방법으로 할 수 있습니다.\n\n제 14 조 (저작권의 귀속 및 이용제한)\n①회사가 작성한 게시물에 대한 저작권은 회사에 있으므로 회원이 회사의 서비스를 이용함으로써 얻은 정보 및 각종 자료는 회사의 명시적인 사전승낙 없이 이용 또는 제3자에게 제공하여서는 아니 됩니다.\n②회사는 회원이 게재한 게시물 중 출처를 밝히지 않은 것은 저작권법에 의거하여 사용할 수 있습니다.\n③회원이 등록한 게시물에 대한 저작권은 해당 저작권자에게 귀속합니다.\n④회원은 전항의 저작권에 대한 권리 중 전시, 복제, 전송, 배포, 홍보할 수 있는 저작재산권의 권리를 회사에 양도하며, 회사는 회원 게시물의 본질을 해(害) 하지 않는 범위 내에서 편집 저작물을 작성하여 사용할 권리를 갖습니다.\n⑤회사는 제4항을 근거로 하여 회원의 동의 없이 제3자에게 게시물을 제공할 수 있습니다.\n⑥회원은 자신이 게재한 게시물에 실명(實名) 또는 별칭 등을 표시할 권리가 있습니다. 단 회원이 본문의 권리를 행사하지 않을 경 우 회사는 그 권리를 포기한 것으로 간주하며, 회사는 별도의 실명 또는 별칭 등의 표시 없이 제4항 및 제5항에 의거 사용합니다.\n⑦회사가 다나와를 운영하는 동안 회원으로부터 제공 받은 게시물은 회원의 탈퇴 후에도 제4항 내지 6항에 의거 유효하게 사용할수 있습니다. 단 회원이 자신의 게시물을 삭제하고자 할 경우에는 회사 운영자에게 전자우편 또는 유선 등의 방법으로 직접 요청해야 합니다.\n\n\n제 5장 기타\n제 15 조 (회사의 면책)\n①회사의 서비스는 회원에게 온라인 거래를 위한 가격비교 정보 등 부가정보를 제공하는 것에 불과하며, 회원에게 직접적인 영향을주는 상행위는 하지 않습니다.\n②회원의 컴핏 이용과 관련하여 서비스 중단이나, 상품 구매시 발생하는 문제 기타 여러 사유로 인하여 회원에게 발생한 손해에 대해 회사는 책임을 지지 않습니다.\n③회사는 회원의 각종 서비스 이용과 관련하여 적합성, 정확성, 시의성(時宜性), 신빙성에 관한 보증 또는 담보책임을 부담하지 않습니다.\n④회사는 회원의 귀책사유로 인한 서비스 이용의 장애 또는 손해에 대하여 책임을 지지 않습니다.\n⑤회사는 회원의 컴퓨터 오류에 의해 손해가 발생한 경우 또는 회원이 신상정보 및 전자우편 주소를 잘못 기재하여 손해가 발생한경우 책임을 지지 않습니다.\n⑦회원이 회사 서비스를 이용하면서 얻은 자료로 인해 물질적?정신적 손해를 입을 경우 책임을 지지 않습니다.\n⑧회사는 회원이 서비스에 게재한 각종 정보, 자료 등에 대해 정확성, 적법성을 보장하지 않습니다.\n⑨회사는 회원 상호간 및 회원과 제3자 상호 간에 서비스를 매개로 발생한 분쟁에 대해 개입할 의무가 없으며, 이로 인한 손해를 배상할 책임도 없습니다.\n⑩회사와 회원은 본 약관 및 각종 회사 규정에 명시된 사항을 따릅니다.\n\n제 16 조 (통지 및 정보의 제공)\n①회사는 회원에게 서비스 이용에 필요가 있다고 인정되는 각종 정보에 대해서 전자우편이나 서신우편 등의 방법으로 회원에게 통지할 수 있습니다.\n②회사는 서비스 개선 및 소개 등의 목적으로 회원의 동의 하에 추가적인 개인 정보를 요구할 수 있습니다.\n③회사는 불특정 다수 회원에 대한 통지의 경우 1주일 이상 회사 게시판에 게재 함으로써 개별 통지에 갈음할 수 있습니다.\n\n제 17 조 (재판권 및 준거법)\n①본 약관에 명시되지 않은 사항은 관계법령과 상관습에 따릅니다.\n②각종 분쟁에 대해 소송이 제기되는 경우 서울중앙지방법원을 합의관할로 합니다.\n\n[부칙] 본 약관은 2018 . 07 . 13 . 부터 적용하고, 2016. 12. 03. 부터 적용되던 종전의 약관은 본 약관으로 대체합니다.\n    ")])])}]
