var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_c('h3',[_vm._v("개인정보취급방침")]),_vm._v(" "),_c('pre',[_vm._v("아이티스트 및 컴핏 사이트 (이하 “회사”라 함)는 헌법이 보장하는 국민의 기본권인 사생활의 비밀과 자유 및 통신의 비밀을 보장 하기 위하여 최선을 다하고 있으며, 이를 위해 회사는 정보통신망 이용 촉진 및 정보보호 등에 관한 법률, 전기통신 사업법, 개인정보 보호 지침 등을 준수 하고 있습니다. 회사는 회원의 기본권 침해 소지를 원천적으로 제거함으로써 정보화 사회에서의 통신의 자유를 보장하고 회원 여러분의 개인정보를 보호하기 위해 개인정보 취급 방침을 수립하고 이를 준수합니다.“개인정보”라 함은 생존하는 개인에 관한 정보이며, 성명ㆍ주민등록번호 등에 의하여 당해 개인을 알아볼 수 있는 부호ㆍ문자ㆍ음성ㆍ음향 및 영상 등의 정보(당해 정보만으로는 특정 개인을 알아볼 수 없는 경우에도 다른 정보와 용이하게 결합하여 알아볼 수 있는 것을 포함)를 말합니다.\n\n1. 개인정보 수집에 대한 동의\n회사는 회원들이 회사의 개인정보취급방침과 이용약관의 내용에 대하여 <동의> 버튼 또는 <취소> 버튼을 클릭할 수 있는 절차를 통해 <동의>버튼을 클릭하면 개인정보 수집에 대해 동의한 것으로 간주합니다.\n\n2. 개인정보의 수집 및 이용 목적\n회사는 회원에게 최적화되고 맞춤화된 서비스를 제공하기 위하여 회원의 개인정보를 수집하고 있으며 , 수집항목에 따른 목적은 다음과 같습니다.\n\n수집항목수집목적 및 이용목적\n성명, 아이디, 비밀번호, 주민등록번호 회원제 서비스 이용에 따른 본인 식별 절차에 이용\n집 주소, 이메일 주소, 메일링 서비스, 전화번호 고지사항 전달, 본인 의사 확인, 불만 처리 등 원활한 의사소통 경로의 확보, 새로운 서비스/신상품이나 이벤트 정보안내, 제휴사에게 회원의 경품 배송지 또는 물품 수령에 필요한 정보제공 등\n기타정보 (생년월일, 직장 업종, 소속직장의 사업자 등록번호, 휴대폰 번호 등)업무와 관련된 통계자료 작성 및 사용을 위하여 회사는 서비스 제공에 필요한 최소한의 정보 및 소정의 신청양식에 따라 이를 수집\n\n\n3. 수집하는 개인정보의 항목 및 수집방법\n가. 개인정보 수집항목\n(1) 필수항목 : 성명, 아이디(ID), 비밀번호, 주민등록번호, 집 주소, 이메일 주소, 전화번호 등\n(2) 선택항목 : 별칭, 메일링 서비스 등\n나. 개인정보 수집방법\n회원은 회원 가입 시 회사와 ㈜한국신용평가 정보간의 제휴내용에 따른 실시간 실명확인 서비스를 통해 실명을 인증 받은 후 회원가입을 하게 되며, 이 때 회사는 회원들로부터 서비스 제공을 위한 필수적인 정보들을 온라인상에서 입력 받고 있습니다.\n\n4. 개인정보의 보유 및 이용기간\n회사의 회원으로서 회사가 제공하는 서비스를 받는 동안 회원의 개인정보는 회사에서 계속 보유하며, 이러한 정보는 서비스 제공을 위해 이용됩니다. 회원의 개인정보는 수집목적 또는 제공 받은 목적이 달성되면 파기하는 것을 원칙으로 합니다. 다만, 아래의 경우에는 회사가 회원의 개인정보를 일정기간 보유합니다.\n가. 상법, 전자 상거래 등에서의 소비자 보호에 관한 법률, 정보통신망 이용 촉진 및 정보보호 등에 관한 법률 등 기타 관계법령의 규정에 의하여 명문화 되어 있는 경우\n①회사 마켓 거래에 있어 계약 또는 청약철회 등에 관련된 제반 기록 : 5년\n②대금결제 및 재화 등의 공급에 관한 기록 : 5년\n③회원의 불만 또는 분쟁처리에 관련된 제반 기록 : 3년\n\n나. 회사는 다음과 같은 행위를 하는 회원이 있을 경우 그 회원의 개인정보를 6개월간 보유하면서 회원의 재가입을 제한할 수 있습니다.\n①회원이 운영자에게 욕설을 하는 경우\n②회원간 분쟁이 발생하는 경우\n③회원이 광고 게시물 및 불법 게시물을 등록하는 경우\n④회원이 타인의 개인정보를 침해하려고 한 경우\n⑤기타 사회 통념 및 상거래 질서를 위반하는 행위를 하는 경우\n\n다. 보존되는 개인정보 항목\n①회원가입시 기재했던 필수항목과 선택항목\n②게시물 기록\n③상거래 기록\n④로그인 기록\n\n5. 개인정보의 파기절차 및 방법\n‘이용자의 개인정보 관리에 관한 사항’에서 설명한 방법에 따라 회원 자신이 직접 개인정보를 삭제하거나 수정한 경우, 가입 해지를 요청한 경우에는 회원의 개인정보는 재생할 수 없는 방법에 의하여 완전히 삭제되며 어떠한 용도로도 열람 또는 이용할 수 없도록 처리됩니다.\n6. 개인정보의 제공 및 공유\n회사는 회원의 「개인정보 수집 및 이용목적」을 통해 고지한 범위 내에서 회원의 개인정보를 이용하며, 이 범위를 초과하여 이용하지 않습니다. 다만, 다음 각호의 경우에는 회사가 각별한 주의를 기울여 회원의 개인정보를 이용하고 있습니다.\n가. 패밀리 사이트\n회원은 하나의 ID로 회사가 운영하는 패밀리 사이트에 한해 별도의 회원가입 절차 없이 편리하게 이용 하실 수 있습니다.\n회원의 최초 이용신청 이후 회사가 운영하는 패밀리 사이트가 늘어난 경우에도 회원은 별도의 회원가입 절차 없이 이용 하실 수 있습니다. 이 경우 회사는 신규 서비스 개시에 대한 정보를 회사 사이트 또는 회원의 이메일 등을 통해 공지합니다.\n비회원이 패밀리 사이트를 이용하여 상품을 구매할 경우 회사는 그 당시 비회원이 제공한 개인정보를 전자 상거래 등에서의 소비자 보호에 관한 법률에 따라 보유할 수 있습니다.\n\n나. 제휴사\n회사는 제휴사와의 서비스 목적을 달성하기 위하여 회원의 사전동의 하에 필요한 최소한의 회원정보를 제휴사에게 제공할 수 있습니다.\n회사의 웹 페이지에 포함된 쇼핑몰 링크 또는 배너를 클릭하여 다른 사이트나 웹 페이지로 옮겨갈 경우 개인정보 취급방침은 그 사이트 운영기관이 게시(揭示)한 방침이 적용되오니 새로 방문한 사이트의 개인정보 취급방침을 반드시 확인하시기 바랍니다.\n\n회사는 서비스의 제공과 관련하여 취득한 회원의 신상 정보를 본인의 동의 없이 제3자에게 누설 또는 배포하지 않으며, 상업적인 목적으로 사용하지 않습니다.\n다만, 다음 각호의 경우에는 예외로 합니다.\n\n가. 회원이 사전에 개인정보 공개에 동의를 한 경우\n나. 서비스 제공에 따른 요금 정산을 위하여 필요한 경우\n다. 통계작성ㆍ학술연구 또는 시장조사를 위하여 필요한 경우로서 특정 개인을 알아볼 수 없는 형태로 재가공한 경우\n라. 다른 법률에 특별한 규정이 있는 경우\n마. 행정관청 및 수사기관이 개인정보 공개를 요구해 오더라도 그것이 재량행위일 경우 회사는 회원의 개인정보를 우선적으로 보호할 것입니다. 다만, 행정관청 및 수사기관의 개인정보 공개 요구가 재량행위의 범위를 넘어서 법적 구속력이 있을 경우 회사는 개인정보를 공개 할 수 있습니다.\n6-1. 개인정보 제3자 제공관련\n* 개인정보 제3자 제공관련 아이티스트 및 컴핏은 원칙적으로 회원의 동의없이 제3자에게 개인정보를 제공하지 않습니다. 단, 컴핏에서 이뤄지는 물품구매, 물품배송, 컴퓨터 방문출장 A/S 서비스, 각종 이벤트에 참여할 경우 주관사 혹은 서비스 대행업체에 개인정보를 제공하는데 대해 동의한 것으로 간주하며, 제공 목적 및 내역 등은 다음과 같습니다.\n가. 개인정보를 제공받는 자\n- 물품배송 및 환불에 필요한 정보를 추가로 수집할 수 있습니다.: 상품의 배송을 위한 배송지 확인\n- 컴퓨터 구매시 '조립비'와 함께 '컴퓨터 방문출장 A/S'를 신청한 소비자에 한해 서비스 대행업체에게 소비자 주문내역을 제공할 수 있습니다.\n- 이벤트 주관사 (GMC, 앱솔루트 코리아,베스텍, 이엠텍, 코아엔에스아이, 쓰리알시스템, AONE, 블레스 정보통신, 뉴젠, 다오코리아 등 그외 다수)\n나. 개인정보를 제공받는 자의 개인정보 이용목적\n회원의 물품구매, 물품배송지 정보, 경품 배송지등 물품 수령에 필요한 정보 제공 및 환불에 필요한 정보제공\n\n다. 제공하는 개인정보의 항목\n성명, 아이디, 집주소, 전화번호, 휴대폰번호, 이메일주소, 소비자 주문내역\n\n라. 개인정보를 제공받자의 개인정보 보유 및 이용기간\n제공일로부터 활용후 즉시파기\n\n\n8. 쿠키(Cookie)의 설치ㆍ운영 및 거부\n쿠키(cookie)는 웹 사이트 서버가 회원의 컴퓨터로 전송하는 소량의 정보이며, 여기에는 방문한 웹 사이트의 정보 및 서비스 제공 시 확인에 필요한 회원의 개인정보 등이 담겨 있습니다. 회원은 쿠키의 수신을 거부하거나 쿠키의 수신에 대하여 경고하도록 사용하는 컴퓨터의 웹 브라우저 설정을 변경할 수 있습니다. 회사는 다음과 같이 웹 서비스 이용에 있어 필요하다고 판단되는 경우 회원의 컴퓨터에 쿠키를 전송 할 수 있습니다.\n가. 개인의 관심 분야에 따라 차별화된 정보를 제공\n나. 회원과 비회원의 접속빈도 또는 이용 시간 등을 분석하여 회원의 취향과 관심 분야를 파악하여 타겟(target) 마케팅에 활용\n다. 관심 있게 둘러 본 품목들에 대해 자취를 추적하여 개인 맞춤 서비스를 제공\n라. 회원들의 습관을 분석하여 서비스 개편 등을 고려하는 척도\n마. 게시판 글 등록\n\n9. 이용자의 개인정보관리(열람, 정정, 삭제 등)에 관한 사항\n회원은 자신의 개인정보를 다음 각 호의 방법에 따라 언제든지 관리할 수 있습니다.\n가. 회원이 회사 사이트에 로그인 하여 직접 관리하는 방법\n나. 회원이 회사 개인정보 관리 책임자 및 담당자에게 서면(팩스 포함) 또는 이메일로 연락하는 방법\n\n회원이 개인정보의 오류에 대한 정정을 요청한 경우, 회사는 정정을 완료하기 전까지 당해 개인정보를 이용 또는 제공하지 않습니다.\n\n회원은 회사에게 자신의 정보 수집과 이용에 대해 동의한 것을 철회할 수 있습니다.\n\n회원은 회사에게 자신의 개인정보를 이용하거나 제3자에게 제공한 내역을 요구할 수 있습니다.\n\n회사는 회원이 허위의 정보를 제공한 것을 신뢰하여 서비스 이용 목적에 따라 제휴사에게 개인정보를 제공한 경우 회사는 허위 사실을 인지한 즉시 개인정보를 정정하여 그 결과를 제휴사에게 통지합니다.\n\n10. 개인정보관련 기술적ㆍ관리적 대책\n회사는 이용자들의 개인정보를 취급함에 있어 개인정보가 분실, 도난, 누출, 변조 또는 훼손되지 않도록 안전성 확보를 위하여 다음과 같은 기술적 대책을 강구하고 있습니다.\n이용자들의 개인정보는 비밀번호에 의해 철저히 보호되고 있습니다. 회원 아이디(ID)의 비밀번호는 본인만이 알고 있으며, 개인정보의 확인 및 변경도 비밀번호를 알고 있는 본인에 의해서만 가능합니다. 따라서 이용자 여러분께서는 비밀번호를 누구에게도 알려주시면 안됩니다. 이를 위해 회사에서는 기본적으로 PC에서의 사용을 마치신 후 온라인상에서 로그아웃(LOG-OUT)하시고 웹브라우저를 종료하도록 권장합니다. 특히 다른 사람과 PC를 공유하여 사용하거나 공공장소(회사나 학교, 도서관, 인터넷 게임방 등)에서 이용한 경우에는 개인정보가 다른 사람에게 알려지는 것을 막기 위해 위와 같은 절차가 더욱 필요할 것입니다.\n\n회사는 해킹이나 컴퓨터 바이러스 등에 의해 회원의 개인정보가 유출되거나 훼손되는 것을 막기 위해 최선을 다하고 있습니다. 개인정보의 훼손에 대비해서 자료를 수시로 백업하고 있고, 최신 백신프로그램을 이용하여 이용자들의 개인정보나 자료가 누출되거나 손상되지 않도록 방지하고 있으며, 암호화통신 등을 통하여 네트워크상에서 개인정보를 안전하게 전송할 수 있도록 하고 있습니다. 그리고 침입차단시스템을 이용하여 외부로부터의 무단 접근을 통제하고 있으며, 기타 시스템적으로 보안성을 확보하기 위한 가능한 모든 기술적 장치를 갖추려 노력하고 있습니다\n\n회사의 개인정보관련 취급 직원은 담당자에 한정시키고 있고 이를 위한 별도의 비밀번호를 부여하여 정기적으로 갱신하고 있으며, 담당자에 대한 수시 교육을 통하여 회사의 개인정보 취급방침의 준수를 항상 강조하고 있습니다. 그리고 사내 개인정보전담기구 등을 통하여 회사 방침의 이행사항 및 담당자의 준수여부를 확인하여 문제가 발견될 경우 즉시 수정하고 바로 잡을 수 있도록 노력하고 있습니다.\n단, 이용자 본인의 부주의나 인터넷상의 문제로 ID, 비밀번호, 주민등록번호 등 개인정보가 유출해 발생한 문제에 대해 회사는 일체의 책임을 지지 않습니다.\n11. 개인정보에 대한 민원처리 및 분쟁해결\n가. 민원처리부서\n커뮤니티지원팀 (010-2390-5384)\n나. 분쟁조정기관\n① 개인정보분쟁조정위원회 (http://www.kopico.or.kr / 전화 :부02-1336)\n② 정보보호마크인증위원회 (http://www.eprivacy.or.kr / 전화 : 02-580-0533~4)\n③ 개인정보 침해신고센터 (http://www.cyberprivacy.or.kr / 전화 1336)\n12. 개인정보관리책임자(CEO) 및 담당자\n성    명 채유철\n소속부서 총괄팀\n직    위 CEO\n전자우편 yucheol3267@naver.com\n전화번호 010-2390-5384\n\n13. 고지의무\n이 개인정보취급방침이 법령, 정책 또는 보안기술의 변경에 따라 내용이 추가, 삭제 및 수정될 경우에는 변경사항의 시행일의 7일전부터 회사 사이트의 공지사항을 통하여 고지할 것 입니다.\n    ")])])}]
